export default {
	data() {
		return {
			lunbo: [],
			tjpic: [],
			top: [],
			num: ""
		}
	},
	onLoad() {
		uni.request({
			url: 'http://musicapi.leanapp.cn/toplist/detail',
			success: (res) => {
				this.top = res.data.list
			}
		})
	},
	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		//歌单详情
		songs(e) {
			uni.navigateTo({
				url: '../tops/tops?id=' + e.id + '&alls=' + e.trackCount + '&sub=' + e.subscribedCount + ''
			})
		}
	}
}
